<template>
    <section>
        <dynamic-form lang="en" ref="AddForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
export default{
  created:function(){
    debugger;
    this.username = this.GetUserInfo.Name;
    this.userid = this.GetUserInfo.ID;
    this.GetCountryData();
    this.GetLeadOwner();
    this.getCustomFieldData();
      
  },
  data(){
    return{
        LeadData:[],
        username: null,
        userid: null,
        FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('FirstName'),
                            name: "FirstName",
                            value: "",
                            placeholder: this.$t('FirstName'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LastName'),
                            name: "LastName",
                            value: "",
                            placeholder: this.$t('LastName'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('LeadEmail'),
                            name: "LeadEmail",
                            value: "",
                            placeholder: this.$t('LeadEmail'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('LeadOwner'),
                            name: "LeadOwner",
                            value: "",
                            placeholder: this.$t('LeadOwner'),
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('LeadDate'),
                            name: "LeadOwner",
                            value: "",
                            placeholder: this.$t('LeadOwner'),
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: this.$t('Status'),
                            config: {
                                options: [{name:'Active',value:1001},{name:'InActive',value:'1002'}],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "Country",
                            value: "",
                            placeholder: this.$t('Country'),
                            config: {
                                options: [],
                                onChange: this.onChangeProfile,
                            },
                            validationRules: {
                                "required": true
                            }
                        },
                    ]
                }
            ],
            buttons: [
                   {
                       type: "submit",
                       class: "btn btn-success",
                       text: "<i class='fa fa-save pr-2'></i>Save",
                   }
               ],
    }
  },
  methods:{
    // getCustomFieldData:function(){
    //     var vm=this;
    //     const params = `id=0&moduleName=CRM&subModuleCode=CRM_LEADS&langCode=en`;
    //     DataService.getFormLayout(params).then(function (response) {
    //         vm.LeadData = response.data.data;
    //     })
    // },
    GetCountryData:function(){
        var vm=this;
        DataService.GetCountryData().then(function(response) { 
            var data=response.data;
            data.forEach(element => {
                vm.FormSchema[0].Data[6].config.options.push({
                    name:element.countrY_FULL_NAME,
                    value:element.id
                })
            });
        })
    },
    GetLeadOwner:function(){
        var vm=this;
        vm.FormSchema[0].Data[3].config.options.push({
            name:vm.username,
            value:vm.userid
        })
    },
    onSubmit:function(){
        var vm=this;
    }
  }
}
</script>
